<template>
  <div class='main'>
    <div class='contain df'>
      <div class='contain-main flex1'>
        <div class='page-main mt20'>
          <el-table
            :data='tableData4'
            stripe
            border
            style='width: 100%'
          >
            <el-table-column
              v-for='item in columns'
              :prop='item.key'
              :label='item.title'
              :key='item.key'
              :fixed='item.fixed'
              :width='item.width'>
              <template slot-scope='scope'>
                <div v-if="item.key === 'operate'">
                  <a class='no-decoration' href='javascript:void(0);'
                     @click='jumpToFlux(scope.row)'>审批流程</a>
                  <a class='no-decoration' href='javascript:void(0);'
                     @click='handleClick(scope.row)'>{{item.formatter ? item.formatter(scope.row[item.key]) :
