        columns: [
          {title: '审批流程名称', key: 'name'},
          {title: '更新人', key: 'person'},
          {title: '更新最后更新时间', key: 'latest'},
          {title: '说明', key: 'mark'},
          {title: '状态', key: 'status', formatter: this.statusFormatter},
          {title: '操作', key: 'operate', formatter: this.statusFormatter, fixed: 'right', width: '170px'}
        ],
