            status: 0,
            person: '小胡',
            mark: '2个步骤',
            operate: 1,
            id: 2
          },
          {
            title: '3333',
            createAt: '2016-05-02',
            status: 2
          },
          {
            title: '44444',
            createAt: '2016-05-02',
            status: 0
          },
        ],
        data: [
          {
            label: '一级 1',
            children: [{
              label: '二级 1-1',
              children: [{
                label: '三级 1-1-1'
              }]
            }]
          },
          {
            label: '一级 2',
            children: [
              {
                label: '二级 2-1',
                children: [{
                  label: '三级 2-1-1'
                }]
              },
              {
                label: '二级 2-2',
                children: [{
                  label: '三级 2-2-1'
                }]
              }
